.table {
    margin: 10% auto;
    display: flex;
    flex-direction: column;
    width: 750px;
    padding: 25px;
    border: 1px solid #d5dddf;
    border-radius: 5px;
    transition: .4s;
    background: #fff;
    color:#323436;
    
}

.table-headline {
    display: flex;
    justify-content: space-between;
    line-height: 25px;
}

.table-close {
    text-decoration: none;
    margin: 0;
    padding: 0;
    font-weight: bold;
    color:#323436;
    font-size: 18px;
}

.table-caption {
    margin-top: 0;
    padding: 0;
    color:#323436;
}

.size-table {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 99999;
    display: none;
    pointer-events: none;
    transition: .4s;
}

.size-table:target {
    transition: .4s;
    display: block;
    pointer-events: auto;
    background: rgba(0,0,0,0.8);
}

table {
    border-collapse: collapse;
    border: 2px solid #323436;
    text-align: center;
}

td {
    padding: 5px;
    padding-right: 10px;
    border: 1px solid #abadaf;
}
  
th {
    padding: 10px;
    border: 1px solid #323436;
}


.link-table {
    float: right;
    margin-top: 10px;
    text-decoration: none;
    color: #aeb2b7;
    transition: .2s;
}

.link-table:hover {
    transition: .4s;
    color: #323436;
}